.teclado {
  box-shadow: 0 0 10px #00000055;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(25px, 1fr));
  grid-auto-rows: 25px;
  gap: 5px;
  padding: 15px;
  max-width: 750px;
}

.teclado-letra {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.6rem;
  background: #222;
  color: #f7f7f7;
  cursor: pointer;
}

.teclado-letra:hover {
  opacity: 0.7;
}

.tecla-seleccionada {
  opacity: 0.7;
  cursor: not-allowed;
  pointer-events: none;
}

@media only screen and (min-width: 600px) {
  .teclado {
    grid-template-columns: repeat(auto-fit, minmax(35px, 1fr));
    grid-auto-rows: 35px;
  }
}
