.resultado {
  display: flex;
  position: relative;
  width: 100%;
  justify-content: space-between;
  gap: 10px;
  border-left: 5px solid #d5d5d5;
  box-shadow: 0 0 6px #00000033;
  padding: 10px;
}

.resultado-usuario {
  width: 70%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
}

.resultado-usuario-titulo {
  width: 100%;
  font-size: 1.5rem;
  font-weight: bold;
  color: #444;
}

.posicion {
  background: #444;
  color: #fff;
  font-size: 2rem;
  padding: 0.1em 0.5em;
  font-weight: bolder;
  border-radius: 10%;
}

.resultado-usuario-fecha {
  font-size: 1.2rem;
  font-weight: 400;
  color: #b1b1b1;
}

.resultado-usuario-boton {
  border: none;
  font-size: 1.2rem;
  padding: 0.5em 1em;
  font-weight: 600;
  background-color: #222222;
  color: #ffffff;
  border-radius: 7px;
  transition: background 0.3s;
}

.resultado-usuario-boton:hover {
  background-color: #ffffff;
  color: #222222;
  outline: 1px solid #222222;
}

.resultado-punteo {
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  font-weight: bold;
  border: 1px solid #e5e5e5;
}

/* MEDIAS */

@media only screen and (min-width: 625px) {
  .resultado {
    padding-left: 30px;
  }
}

@media only screen and (min-width: 625px) {
  .resultado-usuario-titulo {
    width: 40%;
  }
}
