header {
  width: 100%;
}

.nav {
  display: flex;
  background: #200122;
  background: -webkit-linear-gradient(to right, #6f0000, #200122);
  background: linear-gradient(to right, #6f0000, #200122);

  height: 70px;
  width: 100%;
  align-items: center;
  justify-content: center;
  padding: 0px 10px;
}

.nav-brand {
  width: 25%;
  height: 100%;
  display: flex;
  align-items: center;
}

.nav-brand a {
  text-decoration: none;
  color: #ffffff;
  font-size: 2rem;
  font-weight: bold;
  text-shadow: 0 0 10px #ffffff;
}

.nav-brand a:hover {
  opacity: 0.9;
}

.nav-animacion {
  width: 75%;
  display: flex;
  gap: 2%;
  justify-content: center;
  align-items: center;
  opacity: 0;
}

.nav-animacion-letra {
  border: 2px solid #777;
  color: #ffffff;
  padding: 0.5em;
  font-size: 1rem;
}

.h1-titulo {
  font-weight: bold;
  font-size: 2.5rem;
  text-align: center;
  color: var(--azul);
}

.h1-titulo span {
  overflow: hidden;
}

span .text-img {
  height: 2.2rem;
  width: 2.5rem;
}

/* ANIMACIONES LETRAS*/

.letra-1 {
  animation: animacion-letras 0s;
}

.letra-2 {
  animation: animacion-letras 0.5s;
}
.letra-3 {
  animation: animacion-letras 1s;
}
.letra-4 {
  animation: animacion-letras 1.4s;
}
.letra-5 {
  animation: animacion-letras 1.8s;
}
.letra-6 {
  animation: animacion-letras 2.1s;
}
.letra-7 {
  animation: animacion-letras 2.4s;
}

.letra-8 {
  animation: animacion-letras 2.6s;
}

@keyframes animacion-letras {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

/* MEDIA */

@media only screen and (min-width: 350px) {
  .nav-animacion {
    opacity: 1;
  }

  .h1-titulo {
    display: none;
  }
  .nav {
    margin-bottom: 50px;
  }
}
