.modal-eliminar {
  position: fixed;
  top: 0;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #000000bb;
  display: none;
  pointer-events: none;
}

.visible {
  display: flex;
  pointer-events: all;
}

.form-eliminar {
  position: relative;
  height: 200px;
  width: 250px;
  background: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  border-radius: 10px;
}

.boton-cerrar {
  position: absolute;
  margin-top: 5px;
  margin-right: 5px;
  top: 0;
  right: 0;
  background: #000;
  color: #ffffff;
  font-weight: bold;
  border: none;
  border-radius: 3px;
  padding: 5px 10px;
}
.boton-cerrar:hover {
  opacity: 0.8;
}

.form-eliminar-mensaje {
  font-size: 1rem;
  color: #555;
}

.input-eliminar-contraseña {
  font-size: 1rem;
  width: 100%;
  background: #f1f1f1;
  border: none;
  padding: 0.5em 1em;
}

.boton-eliminar {
  width: 50%;
  margin-top: 10px;
  border: none;
  background: #a00;
  color: #ffffff;
  font-size: 1rem;
  padding: 0.5em 1em;
  border-radius: 5px;
}
