.main {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 3px;
  margin-bottom: 50px;
}

canvas {
  background: #ffffff;
  box-shadow: 3px 3px 15px #00000099;
  width: 90%;
  height: 300px;
  margin-bottom: 30px;
}

.informacion {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 40px;
  background: var(--azul);
  width: 90%;
  padding: 0 20px;
  box-shadow: 3px 3px 15px #00000099;
}

.intentos {
  color: #ffffff;
  font-size: 1.6rem;
}

.punteo {
  color: #ffff00;
  font-size: 1.6rem;
}

/* ESPACIOS */
.contenedor-espacios {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 20px;
}
.letra {
  height: 1.2em;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #ffffff;
  border-bottom: 4px solid #777;
  box-shadow: 0px 4px 10px #00000077;
  font-size: 3rem;
}

.icono-modal {
  width: 70px;
  margin-bottom: 20px;
}

/* MEDIA */

@media only screen and (min-width: 625px) {
  canvas,
  .informacion {
    width: 500px;
  }
}
