.modal-pausado {
  position: fixed;
  top: 0;
  height: 100vh;
  width: 100%;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #000000bb;
  display: none;
  pointer-events: none;
}

.contenedor-modal {
  width: 90%;
  background: #f7f7f7;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.modal-titulo {
  font-size: 2.5rem;
  font-weight: 700;
  margin: 0;
}

.modal-mensaje {
  font-size: 1rem;
  color: #007;
}

.modal-punteo {
  font-size: 1.3rem;
  margin-bottom: 20px;
}

.contenedor-opciones {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.boton-variable {
  font-size: 1rem;
  padding: 0.7em 1em;
  background: #8181f7;
  color: #f1f1f1;
  border: none;
  border-radius: 5px;
}

.boton-guardar {
  font-size: 1rem;
  padding: 0.7em 1em;
  background: #444;
  color: #f1f1f1;
  border: none;
  border-radius: 5px;
}

.modal-form {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  display: none;
  pointer-events: none;
  margin-top: 20px;
  border: 0.5px solid #ccc;
  padding: 10px;
}

.modal-form-texto {
  color: #555;
  font-weight: 600;
}

.modal-input-usuario,
.modal-input-contraseña {
  font-size: 1rem;
  width: 90%;
  background: #f1f1f1;
  border: none;
  padding: 0.5em 1em;
  border-radius: 5px;
  margin-bottom: 10px;
}

.modal-boton-submit {
  width: 90%;
  font-size: 1rem;
  border: none;
  padding: 0.5em 1em;
  background: #111;
  color: #f1f1f1;
  border-radius: 5px;
}

.modal-boton-submit:hover,
.boton-guardar:hover,
.boton-variable:hover {
  opacity: 0.8;
}

/* MEDIAS */

@media only screen and (min-width: 425px) {
  .contenedor-modal {
    max-width: 450px;
  }
}
