* {
  box-sizing: border-box;
}

:root {
  font-size: 10px;
  --azul: #01015e;
  --verde: #008f39;
  --borde: #e5e5e5;
}

body {
  margin: 0;
  background-color: #f1f3f4;
}

.container {
  display: flex;
  justify-content: center;
  width: 90%;
}

@media only screen and (min-width: 600px) {
  :root {
    font-size: 15px;
  }
}
