.resultados {
  width: 100%;
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #ffffff;
}

.titulo-resultados {
  font-weight: bold;
  font-size: 2.5rem;
  text-align: center;
  color: #555;
}

.contenedor-resultados {
  width: 90%;
  padding: 10px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* VACIO */
.vacio {
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border: 2px dashed #c5c5c5;
}
